  function createSubscription({ customerId, paymentMethodId, priceId, defaultTaxRates, cancelAt }) {
    return (
      API.post('billing/subscription/create', '/billing/subscription/create', { body: { customerId, paymentMethodId, priceId, defaultTaxRates, cancelAt } })
        // If the card is declined, display an error to the user.
        .then((result) => {
          if (!result.status && result.error) {
            throw result.error; // error associating payment method with customer
          }
          console.log('[createSubscription result]', result);
          return result;
        })
        .then((result) => {
          return {
            paymentMethodId: paymentMethodId,
            priceId: priceId,
            defaultTaxRates: defaultTaxRates,
            subscription: result.subscription,
          };
        })
        // Some payment methods require a customer to be on session
        // to complete the payment process. Check the status of the
        // payment intent to handle these actions.
        .then(handlePaymentThatRequiresCustomerAction)
        // If attaching this card to a Customer object succeeds,
        // but attempts to charge the customer fail, you
        // get a requires_payment_method error.
        .then(handleRequiresPaymentMethod)
        // No more actions required. Provision your service for the user.
        .then(onSubscriptionComplete)
        .catch((error) => {
          // An error has happened. Display the failure to the user here.
          // We utilize the HTML element we created.

          // todo: implement error ui
          //showCardError(error);
          console.log(error)
        })
    );
  }

  function handlePaymentThatRequiresCustomerAction({ subscription, invoice, priceId, defaultTaxRates, paymentMethodId, isRetry }) {
    if (subscription && subscription.status === 'active') {
      // subscription is active, no customer actions required.
      return { subscription, priceId, defaultTaxRates, paymentMethodId };
    }

    // if it's a first payment attempt, the payment intent is on the subscription latest invoice.
    // if it's a retry, the payment intent will be on the invoice itself.
    let paymentIntent = invoice
      ? invoice.payment_intent
      : subscription.latest_invoice.payment_intent;

    if (
      paymentIntent.status === 'requires_action' ||
      (isRetry === true && paymentIntent.status === 'requires_payment_method')
    ) {
      return stripe
        .confirmCardPayment(paymentIntent.client_secret, { payment_method: paymentMethodId })
        .then((result) => {
          if (result.error) {
            // start code flow to handle updating the payment details.
            // display error message in your UI.
            // the card was declined (i.e. insufficient funds, card has expired, etc).
            throw result;
          } else {
            if (result.paymentIntent.status === 'succeeded') {
              // show a success message to customer.
              // there's a risk of the customer closing the window before the callback. watch webhook endpoints to mitigate.
              return {
                priceId: priceId,
                defaultTaxRates: defaultTaxRates,
                subscription: subscription,
                invoice: invoice,
                paymentMethodId: paymentMethodId,
              };
            }
          }
        })
        .catch((error) => {
          
          // todo: implement error ui
          // displayError(error);
          console.log(error);
        });
    } else {
      // No customer action needed.
      return { subscription, priceId, defaultTaxRates, paymentMethodId };
    }
  }

  function onSubscriptionComplete(result) {
    // Payment was successful.
    if (result.subscription.status === 'active') {
      // Change your UI to show a success message to your customer.
      // Call your backend to grant access to your service based on
      // `result.subscription.items.data[0].price.product` the customer subscribed to.
    }
  }

  function handleRequiresPaymentMethod({ subscription, paymentMethodId, priceId, defaultTaxRates }) {
    if (subscription.status === 'active') {
      // subscription is active, no customer actions required.
      return { subscription, priceId, paymentMethodId };
    } else if (
      subscription.latest_invoice.payment_intent.status ===
      'requires_payment_method'
    ) {
      // Using localStorage to manage the state of the retry here,
      // feel free to replace with what you prefer.
      // Store the latest invoice ID and status.
      localStorage.setItem('latestInvoiceId', subscription.latest_invoice.id);
      localStorage.setItem(
        'latestInvoicePaymentIntentStatus',
        subscription.latest_invoice.payment_intent.status
      );
      throw { error: { message: 'Your card was declined.' } };
    } else {
      return { subscription, priceId, defaultTaxRates, paymentMethodId };
    }
  }

  function retryInvoiceWithNewPaymentMethod({ customerId, paymentMethodId, invoiceId, priceId, defaultTaxRates }) {
    return (
      API.post(
        'billing/invoice/retry',
        '/billing/invoice/retry',
        {
          body: { customerId, paymentMethodId, invoiceId }
        }
      )
        // If the card is declined, display an error to the user.
        .then((result) => {
          if (result.error) {
            // The card had an error when trying to attach it to a customer.
            throw result;
          }
          return result;
        })
        // Normalize the result to contain the object returned by Stripe.
        // Add the additional details we need.
        .then((result) => {
          return {
            // Use the Stripe 'object' property on the
            // returned result to understand what object is returned.
            invoice: result,
            paymentMethodId: paymentMethodId,
            priceId: priceId,
            isRetry: true,
          };
        })
        // Some payment methods require a customer to be on session
        // to complete the payment process. Check the status of the
        // payment intent to handle these actions.
        .then(handlePaymentThatRequiresCustomerAction)
        // No more actions required. Provision your service for the user.
        .then(onSubscriptionComplete)
        .catch((error) => {
          // An error has happened. Display the failure to the user here.
          // We utilize the HTML element we created.

          // todo: implement error ui
          // displayError(error);
          console.log(error);
        })
    );
  }

  function parseStripeAddress(countryCode, viesAddress) {
    switch (countryCode) {
      case 'BG':
        return {
          line1: viesAddress.match(/^(.*)\s\./i)[1],
          city: viesAddress.match(/\.(.*)\s[0-9]+$/i)[1],
          state: viesAddress.match(/\.(.*),/i)[1],
          postal_code: viesAddress.match(/\s([0-9]*)$/i)[1],
          country: countryCode
        };
        break;
      default:
        return {
          line1: viesAddress,
          country: countryCode
        };
        break;
    }
  }

  async function handleSubmit(event) {
    event.preventDefault();
    setIsLoading(true);
    //setShowForm(false);

    // associate name, address, telephone, vat number with cognito user
    Auth.currentAuthenticatedUser().then(async(user) => {
      Auth.updateUserAttributes(user,
        {
          name: (vatEntity.name || fields.name),
          phone_number: fields.telephone,
          address: (vatEntity.address || fields.address),
          'custom:vat_number': (vatEntity.valid) ? `${vatEntity.countryCode}${vatEntity.vatNumber}` : null
        })
        .then(response => console.log('[Auth.updateUserAttributes response]', response))
        .catch(error => console.log('[Auth.updateUserAttributes error]', error));
      /*
      todo:
      - generate invoice
      - create stripe subscription
      - trigger stripe payment method collection
      - confirm booking
        - show confirmation
        - send email confirmation
      */

      // associate name, address, telephone, vat number with stripe customer
      API.post('billing/customer/update', '/billing/customer/update',
        {
          body: {
            facilityId: '7edad966-198d-4ee2-8802-02f188e8f62c',
            customerId: user.attributes['custom:stripe_customer_id'],
            ...((vatEntity.valid && vatEntity.name) || fields.name) && { name: (vatEntity.name || fields.name) },
            ...(fields.telephone) && { phone: fields.telephone },
            ...((vatEntity.valid && vatEntity.address) || fields.address) && { address: parseStripeAddress(vatEntity.countryCode, vatEntity.address) },
            ...(vatEntity.valid) && { taxId: { type: 'eu_vat', value: `${vatEntity.countryCode}${vatEntity.vatNumber}` } }
          }
        })
        .then(response => console.log('[API.post (billing/customer/update) response]', response))
        .catch(error => console.log('[API.post (billing/customer/update) error]', error));

      // begin stripe subscription specific processing
      if (!stripe || !elements) {
        return;
      }
      const cardElement = elements.getElement(CardElement);
      const latestInvoicePaymentIntentStatus = localStorage.getItem(
        'latestInvoicePaymentIntentStatus'
      );
      const { error, paymentMethod } = await stripe.createPaymentMethod({
        type: 'card',
        card: cardElement,
      });
      if (error) {
        console.log('[stripe.createPaymentMethod error]', error);
        switch (error.type) {
          case 'validation_error':
            if ('card' in validationSuccesses) {
              let {card, ...validationSuccessesWithCardSuccessesRemoved} = validationSuccesses;
              setValidationSuccesses(validationSuccessesWithCardSuccessesRemoved);
            }
            setValidationErrors({
              ...validationErrors,
              card: error.message
            });
            setIsLoading(false);
            break;
          default:
            break;
        }
      } else {
        console.log('[stripe.PaymentMethod created]', paymentMethod);

        setValidationSuccesses({ ...validationSuccesses, card: true });
        if ('card' in validationErrors) {
          let {card, ...validationErrorsWithCardErrorsRemoved} = validationErrors;
          setValidationErrors(validationErrorsWithCardErrorsRemoved);
        }

        const paymentMethodId = paymentMethod.id;
        const customerId = user.attributes['custom:stripe_customer_id'];
        const priceId = getUnit().price[fields.paymentFrequency].stripe.priceId;
        const defaultTaxRates = getUnit().price[fields.paymentFrequency].stripe.defaultTaxRates;
        const cancelAt = moment(fields.to).format('X');
        if (latestInvoicePaymentIntentStatus === 'requires_payment_method') {
          // Update the payment method and retry invoice payment
          const invoiceId = localStorage.getItem('latestInvoiceId');
          retryInvoiceWithNewPaymentMethod({
            customerId,
            paymentMethodId,
            invoiceId,
            priceId,
            defaultTaxRates
          });
        } else {
          // Create the subscription
          createSubscription({ customerId, paymentMethodId, priceId, defaultTaxRates, cancelAt })
            .then(response => {
              setIsLoading(false);
              //response.subscription.id
            })
            .catch(error => console.log('[createSubscription error]', error));
        }
      }
      // end stripe subscription specific processing

    })
    .catch(error => console.log('[Auth.currentAuthenticatedUser error]', error));
  }

